@font-face {
  font-family: "Forum";
  font-weight: 400;
  font-style: normal;
  src: url("../fonts/Forum.eot");
  src: local('Forum'),
  url("../fonts/Forum.eot?#iefix") format("embedded-opentype"),
  url("../fonts/Forum.woff") format('woff'),
  url("../fonts/Forum.ttf") format('truetype');
}

@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "SF UI Display";

  font-display: swap;
  src: local("SF UI Display"),
  url("../fonts/SF UI Display.woff2") format("woff2"),
  url("../fonts/SF UI Display.woff") format("woff");
}


